  .swiper-button-prev,
  .swiper-button-next {
    display: flex;
  }
}

/* 前へ次への矢印カスタマイズ */
.swiper-button-prev::after,
.swiper-button-next::after {
  background-repeat: no-repeat;
  background-size: contain;
  content: "";
  width: 100%;
  height: 100%;
  margin: auto;
}

@media screen and (min-width: 768px) {
  .swiper-button-prev {
    top: 1%;
    left: 90%;
  }
}

@media screen and (min-width: 768px) {
  .swiper-button-next {
    top: 1%;
    left: 76%;
  }
}

/* 前への矢印カスタマイズ */
.swiper-button-prev::after {
  background-image: url(/dist/assets/images/common/campaign-icon2.svg);
}

/* 次への矢印カスタマイズ */
.swiper-button-next::after {
  background-image: url(/dist/assets/images/common/campaign-icon1.svg);
}

